export default {
	props:{
		items:{
			type: Array
		},
		name:{
			type: String,
			default: '测试'
		},
		initValue: {
			type: [Number,String]
			
		}
	},
	data() {
		return {
			key: 0
		};
	},
	onReady:function(){
		this.items.forEach((item,index)=>{
			if (item.id == this.initValue){
				this.key = item.id
				
			}
		})
	},
	methods:{
		buttonChange:function(id){
			this.key = id
			this.$emit("buttonChange",id)
		}
	}
}
